import React from "react";
import { AppBar, Box, IconButton, Badge, Toolbar } from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";
import MenuIconOpen from "@mui/icons-material/MenuOpen";
import MailIcon from "@mui/icons-material/Mail";
import NotificationsIcon from "@mui/icons-material/Notifications";
import CustomSwitch from "./CustomSwitch"; // Giả sử bạn đã tạo component CustomSwitch

const Header = ({ isOpen, isHovered, toggleDrawer, changeMode, theme }) => {
  return (
    <Box
      component="header"
      sx={{
        width: `calc(100% - ${isOpen || isHovered ? 240 : 64}px)`, // Điều chỉnh độ rộng khi thu gọn
        backgroundColor: (theme) => theme.palette.background.paper, // Sử dụng background từ theme
        padding: 2,
        position: "fixed",
        top: 0,
        left: `${isOpen || isHovered ? 240 : 64}px`, // Điều chỉnh vị trí khi thu gọn
        zIndex: 1201, // Đảm bảo Header luôn nằm trên các phần tử khác
      }}
    >
      <AppBar
        position="fixed"
        sx={{
          width: `calc(100% - ${isOpen || isHovered ? 240 : 64}px)`,
          backgroundColor: (theme) => theme.palette.primary.main, // Sử dụng màu chính từ theme cho AppBar
          boxShadow: 3, // Áp dụng shadow từ theme
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            edge="start"
            sx={{ mr: 2 }}
            onClick={toggleDrawer}
          >
            {isOpen || isHovered ? <MenuIconOpen /> : <MenuIcon />}
            {/* Điều kiện thay đổi icon */}
          </IconButton>

          <Box sx={{ flexGrow: 1 }} />
          <Box sx={{ display: { xs: "none", md: "flex" } }}>
            <IconButton size="large" color="inherit">
              <Badge badgeContent={0} color="error">
                <MailIcon />
              </Badge>
            </IconButton>
            <IconButton size="large" color="inherit">
              <Badge badgeContent={0} color="error">
                <NotificationsIcon />
              </Badge>
            </IconButton>
          </Box>
          <Box
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <CustomSwitch
              checked={theme.palette.mode === "dark"} // Kiểm tra chế độ hiện tại
              onChange={changeMode} // Hàm đổi chế độ
            />
          </Box>
        </Toolbar>
      </AppBar>
      <Box sx={{ marginTop: 8 }}></Box> {/* Để tránh bị che khuất bởi AppBar */}
    </Box>
  );
};

export default Header;
